import { RemoveTags } from './../../../../templates/pipes/RemoveTags.pipe';
import { JSONBuilder } from './../../../../+builder/services/JSONBuilder.service';
import { Component, Input, OnInit, AfterViewInit, ViewEncapsulation, OnChanges, ChangeDetectorRef } from '@angular/core';
import { CalculatorAnalytics } from '../../services/calculator-analytics.service';
import { JSONElement } from '../../../../+builder/services/JSONElement.service';

declare var jQuery: any;
declare var moment: any;

@Component({
  selector: 'og-user-details-popup',
  templateUrl: './user_details_popup.component.html',
  encapsulation: ViewEncapsulation.None,
})

export class UserDetailsPopupComponent implements OnInit, OnChanges, AfterViewInit {
  @Input() visitorKey: any;
  @Input() lead: any;
  statsResult: any;
  time: any;
  visitors: string[] = [];
  selectedIndex: number = 0;
  dataSet: boolean = false;
  constructor(public _calculatorAnalytics: CalculatorAnalytics,
    public cdr: ChangeDetectorRef, public jsonBuilderHelper: JSONBuilder,
    public jsonElementHandler: JSONElement
  ) {

  }

  ngOnInit() {
    //Code
  }

  ngAfterViewInit() {
    //code
    jQuery(document).on('click', '.clear-set', () => {
      //Hide the menus if visible
      jQuery('.user-detail-outer').removeClass('open');
    });
  }

  ngOnChanges() {
    let self: any = this;
    this.visitors = [];
    this.dataSet = false;
    this.statsResult = undefined;
    if (this.visitorKey) {
      jQuery('a.vd').map((index, tag) => {
        if (tag.dataset.key == self.visitorKey) { self.selectedIndex = index; }
        self.visitors.push(tag.dataset.key);
      });

      this.getStatsInfo();
    }
  }

  getStatsInfo() {
    /* get leads of user on page */
    this._calculatorAnalytics.getStats(this.visitorKey, this.jsonBuilderHelper.getJSONBuilt() ? this.jsonBuilderHelper.getJSONBuilt()._id : '')
      .subscribe(
        (response: any) => {
          /** intialize data table */
          this.statsResult = response;
          this.time = moment(this.statsResult.leads.createdAt).format("MM-DD-YYYY hh:mm A");
          var heightWindow = jQuery(window).height() - 180;
          setTimeout(function () {
            jQuery('.outer-slim').css('height', heightWindow);
          }, 500);
          jQuery(window).on("resize", function () {
            heightWindow = jQuery(window).height() - 180;
            jQuery('.outer-slim').css('height', heightWindow);
          });
          if (this.statsResult.clearBitData && !this.statsResult.clearBitData.error) {
            // this.statsResult.clearBitData = this.simplifyData(this.statsResult.clearBitData)
            this.dataSet = true;
          }
          this.cdr.detectChanges();
        },
        (error: any) => {
          console.log(error);
        }
      );
  }

  previewKeyInfo(state: string) {
    if (state == 'prev')
      this.selectedIndex--;
    else
      this.selectedIndex++;

    this.dataSet = false;
    this.statsResult = undefined;
    this.visitorKey = this.visitors[this.selectedIndex];
    this.getStatsInfo();

  }

  answerHtml(stat: any) {
    if (stat.questionType == 'file') { // for img show link
      return (stat.value.replace(/,\s*$/, "")); 
    } else {
      let answer = ((stat.label.replace(/,\s*$/, "").trim()) ?
        (RemoveTags.prototype.transform(stat.label)) :
        (stat.value.replace(/,\s*$/, "").trim()) ? (RemoveTags.prototype.transform(stat.value)) : stat.link.map((link) => { return this.jsonElementHandler.iconArray.indexOf(link) !== -1 ? `<i class="material-icons">${link}</i>` : link; }));
      return answer;
    }
  }

  simplifyData(clearBitData) {
    let data = this.simplifyKeys(clearBitData);
    return data;
  }

  simplifyKeys(obj) {
    Object.keys(obj).map(key => {
      if (obj[key] == null || obj[key] == undefined)
        delete obj[key];
      else if (obj[key] instanceof Object && obj[key] instanceof Array) {
        if (obj[key].length < 1)
          delete obj[key];
      }
      else if (obj[key] instanceof Object) {
        obj[key] = this.simplifyKeys(obj[key]);
        if (Object.keys(obj[key]).length < 1)
          delete obj[key];
      }
    })
    return obj;
  }

  isArray(obj) {
    return obj instanceof Object && obj instanceof Array;
  }

  isObject(obj) {
    return !(obj instanceof Object && obj instanceof Array) && !this.isVariable(obj);
  }

  isVariable(obj) {
    return (['number', 'string'].indexOf(typeof obj) >= 1);
  }

  getKeys(obj) {
    return Object.keys(obj);
  }

  stripProtocol(str: string) {
    return str.replace(/(^\w+:|^)\/\//, '');
  }

}
