import { Datatable } from './../../../../../shared/interfaces/datatable.interface';
import { LoggedIn } from './../../../../../shared/interfaces/logged-in.interface';
import { BuilderService } from './../../../../+builder/services/builder.service';
import { Component, ViewChild, OnInit, AfterViewInit, ViewEncapsulation, Input, OnChanges, OnDestroy } from '@angular/core';
import { CalculatorAnalytics } from '../../services/calculator-analytics.service';
import { Subscription } from 'rxjs/Subscription';
import { CookieService } from '../../../../../shared/services/cookie.service';
import { SubDomainService } from '../../../../../shared/services/subdomain.service';
import { DateRangePickerComponent } from '../date_range_picker/date_range_picker.component';


declare var jQuery: any;
declare var google: any;
declare var Highcharts: any;

@Component({
  selector: 'og-traffic-details',
  templateUrl: './traffic_details.component.html',
  encapsulation: ViewEncapsulation.None,
})

export class TrafficDetailsComponent implements AfterViewInit, OnChanges, OnDestroy, OnInit {
  @ViewChild(DateRangePickerComponent) public drPicker: DateRangePickerComponent;
  @Input() calc: any;
  @Input() date: any;
  calc_id: string;
  geoLoader: string = 'loading';
  deviceLoader: string = 'loading';
  socialLoader: string = 'loading';
  browserLoader: string = 'loading';
  limit_alert: boolean = false;
  postData: any = {};
  currentCompany: any;
  storage:any;
  public subs: Subscription[] = [];

  constructor(
    public _calcAnalytics: CalculatorAnalytics,
    public _cookieService: CookieService,
    public _subDomainService: SubDomainService,
    public builderService: BuilderService
  ) {
    this.currentCompany = _subDomainService.currentCompany;
  }

  ngAfterViewInit() {
    //Code
  }

  ngOnInit() {
    this.checkTrafficLimit();
    const cookie = this._cookieService.readCookie('storage');
    this.storage = cookie != null ? JSON.parse(cookie) : '';
  }

  checkTrafficLimit() {
    if (!this.builderService.isDemo)
      this.limit_alert = this.currentCompany.current_limit.traffic === -1 || this.currentCompany.current_limit.traffic > this.currentCompany.current_usage.traffic ? false : true;
    else
      this.limit_alert = false;
    //this.limit_alert  = true;
    //console.log(this.limit_alert, 'limit alert');
  }

  ngOnChanges() {
    if (this.calc) {
      if (this.calc.liveApp)
        this.calc_id = this.calc._id;
      else
        this.calc_id = this.calc.parentApp;

      // let {start_date, end_date} = this.drPicker.getStartEndDates(this.calc.createdAt, new Date());
      this.postData.start_date = this.date.start_date;
      this.postData.end_date = this.date.end_date;

      this.postData.calc_id = this.calc_id;
      //get stats
      this.getStats();
    }
  }

  initPieChart(graphData: any[], drawingArea: string) {
    let data = [];
    for (let i = 1; i < graphData.length; i++) {
      let dataObj = {};
      dataObj['name'] = graphData[i][0];
      dataObj['y'] = graphData[i][1];
      data.push(dataObj);
    }
    // Build the chart
    Highcharts.chart(drawingArea, {
      colors: ['#f38630', '#69d2e7', '#e0e4cc'],
      chart: {
        plotBackgroundColor: null,
        plotBorderWidth: null,
        plotShadow: false,
        type: 'pie',
        height: 220,
        width: 500
      },
      title: {
        text: ''
      },
      tooltip: {
        pointFormat: '<b>{point.y} ({point.percentage:.1f}%)</b>'
      },
      exporting: {
        enabled: false
      },
      plotOptions: {
        pie: {
          allowPointSelect: false,
          cursor: 'pointer',
          dataLabels: {
            enabled: false
          },
          showInLegend: true
        }
      },
      legend: {
        labelFormat: '{name} ({percentage:.1f}%)',
        align: 'right',
        layout: 'vertical',
        verticalAlign: 'top'
      },
      credits: {
        enabled: false
      },
      series: [{
        name: '',
        colorByPoint: true,
        data: data
      }]
    });
  }

  getStats() {
    //geo location data
    this.subs.push(this.getGeoStats());
    //device data
    this.subs.push(this.getDeviceStats());
    //social data
    this.subs.push(this.getSocialStats());
    //browser data
    this.subs.push(this.getBrowserStats());
  }

  getGeoStats() {
    this.geoLoader = 'loading';
    this.postData.type = 'country';
    return this._calcAnalytics.getTrafficStats(this.postData)
      .subscribe(
      (response: any[]) => {
        if (!response.length || (response.length == 1 && response[0][0] == '(not set)')) {
          this.geoLoader = 'Available Soon !';
        } else {
          response = response.map(val => [val[0], Number(val[2])]);
          response.unshift(['Country', 'Page Views']);
          this.drawRegionsMap(response);
          this.geoLoader = 'done';
        }
      },
      (error: any) => {
        this.geoLoader = 'Something Went Wrong !';
      }
      );
  }

  getDeviceStats() {
    this.deviceLoader = 'loading';
    this.postData.type = 'deviceCategory';
    return this._calcAnalytics.getTrafficStats(this.postData)
      .subscribe(
      (response: any[]) => {
        if (response.length) {
          response = response.map(val => {
            if (val[0] == '(not set)')
              val[0] = 'Unknown device';
            return response = [val[0], Number(val[2])];
          });
          response.unshift(['Devices', 'Page Views']);
          this.initPieChart(response, 'piechart');
          this.deviceLoader = 'done';
        } else {
          this.deviceLoader = 'Available Soon !';
        }
      },
      (error: any) => {
        this.deviceLoader = 'Something Went Wrong !';
      }
      );
  }

  getSocialStats() {
    this.socialLoader = 'loading';
    this.postData.type = 'socialNetwork';
    return this._calcAnalytics.getTrafficStats(this.postData)
      .subscribe(
      (response: any[]) => {
        if (response.length) {
          response = response.map(val => {
            if (val[0] == '(not set)')
              val[0] = 'Direct Access';
            return response = [val[0], Number(val[2])];
          });
          response.unshift(['Social', 'Page Views']);
          this.initPieChart(response, 'piechart1');
          this.socialLoader = 'done';
        } else {
          this.socialLoader = 'Available Soon !';
        }
      },
      (error: any) => {
        this.socialLoader = 'Something Went Wrong !';
      }
      );
  }

  getBrowserStats() {
    this.browserLoader = 'loading';
    this.postData.type = 'browser';
    return this._calcAnalytics.getTrafficStats(this.postData)
      .subscribe(
      (response: any[]) => {
        if (response.length) {
          response = response.map(val => [val[0], Number(val[2])]);
          response.unshift(['Browser', 'Page Views']);
          this.initPieChart(response, 'piechart2');
          this.browserLoader = 'done';
        } else {
          this.browserLoader = 'Available Soon !';
        }
      },
      (error: any) => {
        this.browserLoader = 'Something Went Wrong !';
      }
      );
  }

  drawRegionsMap(graphData: any[]) {
    var data = google.visualization.arrayToDataTable(graphData);
    var options = {
      'width': 250, 'height': 180,
      colors: ['#ce5050', '#f17a52', '#f9de71'],
      legend: 'none'
    };
    var chart = new google.visualization.GeoChart(document.getElementById('regions_div'));
    chart.draw(data, options);
  }

  drawPieChart(graphData: any[], drawingArea: string) {
    var data = google.visualization.arrayToDataTable(graphData);
    var options = {
      'width': '500', 'height': '220',
      backgroundColor: 'transparent',
      legend: { textStyle: { color: '#8c9194', fontSize: '11px', marginBottom: '5px' } },
      colors: ['#f38630', '#69d2e7', '#e0e4cc']
    };

    var chart = new google.visualization.PieChart(document.getElementById(drawingArea));
    chart.draw(data, options);
  }

  // onDateSelect(date: any) {
  //   this.postData.start_date = date.start_date;
  //   this.postData.end_date = date.end_date;
  //   //refresh stats
  //   this.getStats();
  // }

  ngOnDestroy() {
    this.unsubscribe();
  }

  unsubscribe() {
    this.subs.forEach(sub => sub.unsubscribe());
  }
}
