export class UserDetailsDatatable {
    limits: number[] = [10, 25, 50, 100];
    current_limit: number = 100;
    total_pages: number = 1;
    search: string = '';
    current_page: number = 1;
    leadColumns: any[] = [];
    leadColumnsDescription: any [] = [];
    visitColumnsDescription: any[] = [];
    visitColumns: any[] = [];
    data: any[] = [];
    questionAnsweredList: any = [];

    constructor() {
    }

    arrayOf(): number[] {
        let array: number[] = [];
        let upper_limit: number;
        if (this.total_pages < 5)
            upper_limit = this.total_pages;
        else
            upper_limit = 5;

        for (let i = 1; i <= upper_limit; i++)
            array.push(i);

        return array;
    }

    paging(num: number) {
        this.current_page = num;
    }

    limitChange(event: any) {
        this.current_page = 1;
        this.current_limit = Number(event.target.value);
    }

    previous() {
        if (this.current_page != 1) {
            this.current_page--;
        }
    }

    next() {
        if (this.current_page != this.total_pages) {
            this.current_page++;
        }
    }

    searchData() {
        this.current_page = 1;
    }

    setValues(total_pages: number, data: any[], columns: any[], columneDescription, isLead: boolean, questionAnsweredList: any[]) {
        this.total_pages = total_pages;
        this.data = data
        this.questionAnsweredList = questionAnsweredList;
        if (isLead){
            this.leadColumns = this.setColumnsVisibility(columns, isLead);
            this.leadColumnsDescription = columneDescription;
        }  
        else {
            this.visitColumns = this.setColumnsVisibility(columns, isLead);
            this.visitColumnsDescription = columneDescription;
        }
    }

    // outcomeFilter(){
    //     let data =[];
    //     const index = 
    //     this.data.filter(d=>{
    //         const actualText = 
    //         index= 
    //     })
    // }

    setColumnsVisibility(_columns: any, isLead: boolean) {
        let dropdownColumns: any[] = [];
        let columns = isLead ? this.leadColumns : this.visitColumns;

        for (let column in _columns) {
            let isColAvailable = columns.filter((col) => col['title'] == _columns[column]);
            dropdownColumns.push(
                {
                    visible: isColAvailable.length ? isColAvailable[0]['visible'] : true,
                    title: _columns[column]
                }
            );
        }
        return dropdownColumns;
    }

    resetData() {
        this.total_pages = 1;
        this.current_page = 1;
        this.data = [];
    }
}
