  questionAnswerFilterCheck(questionList: any) {
    let questionDetails = []
    for (let item of questionList) {
      const questionNumber = parseInt(item.actual_name.split('_').pop());
      let valueCheck = [];
      if (item.key !== 7 && item.key !== 8) {
        valueCheck = item.value.filter(d => d.value);
      }
      if (item.key == 7 || item.key == 8 || valueCheck) {
        (item.type == 'int' ? this.filterIntegerQuestionQuery(item, questionNumber - 1, questionDetails) : this.filterStringQuestionQuery(item, questionNumber - 1, questionDetails));
        this.leadDataTableRef.questionAnsweredList = JSON.parse(JSON.stringify(questionDetails));
        questionDetails = [];
      }
    }

  }

  filterStringQuestionQuery(item, questionNumber, questionDetails) {
    let values = [];
    if (item.key !== 7 && item.key !== 8) {
      values = item.value.map(v => v.value);
    }
    switch (item.key) {
      case 1: {
        this.leadDataTableRef.data = this.leadDataTableRef.data.filter((val, index) => {
          if (this.leadDataTableRef.questionAnsweredList[index][questionNumber] && this.leadDataTableRef.questionAnsweredList[index][questionNumber].label) {
            const valIndex = values.findIndex(v => this.leadDataTableRef.questionAnsweredList[index][questionNumber].label.toLowerCase() == v.toLowerCase());
            if (valIndex !== -1) {
              questionDetails.push(this.leadDataTableRef.questionAnsweredList[index]);
              return val;
            }
          }
        });
        break;
      }
      case 2: {
        this.leadDataTableRef.data = this.leadDataTableRef.data.filter((val, index) => {
          if (!this.leadDataTableRef.questionAnsweredList[index][questionNumber]) {
            questionDetails.push(this.leadDataTableRef.questionAnsweredList[index]);
            return val;
          } else {
            const valIndex = values.findIndex(v => !this.leadDataTableRef.questionAnsweredList[index][questionNumber].label && this.leadDataTableRef.questionAnsweredList[index][questionNumber].label.toLowerCase() == v.toLowerCase());
            if (valIndex == -1) {
              questionDetails.push(this.leadDataTableRef.questionAnsweredList[index]);
              return val;
            }
          }
        });
        break;
      }
      case 3: {
        this.leadDataTableRef.data = this.leadDataTableRef.data.filter((val, index) => {
          if (this.leadDataTableRef.questionAnsweredList[index][questionNumber] && this.leadDataTableRef.questionAnsweredList[index][questionNumber].label && this.leadDataTableRef.questionAnsweredList[index][questionNumber].label != 'Not Answered') {
            const valIndex = values.findIndex(v => {
              const regx = new RegExp(".*" + v + ".*", "i");
              if (this.leadDataTableRef.questionAnsweredList[index][questionNumber].label.match(regx)) {
                return true;
              }
            });
            if (valIndex != -1) {
              questionDetails.push(this.leadDataTableRef.questionAnsweredList[index]);
              return val;
            }
          }
        });
        break;
      }
      case 4: {
        this.leadDataTableRef.data = this.leadDataTableRef.data.filter((val, index) => {
          if (this.leadDataTableRef.questionAnsweredList[index][questionNumber].label == 'Not Answered' || !this.leadDataTableRef.questionAnsweredList[index][questionNumber] || !this.leadDataTableRef.questionAnsweredList[index][questionNumber].label) {
            questionDetails.push(this.leadDataTableRef.questionAnsweredList[index]);
            return val;
          } else {
            const valIndex = values.filter(v => {
              const regx = new RegExp("^((?!" + v + ").)*$", "i");
              if (this.leadDataTableRef.questionAnsweredList[index][questionNumber].label.match(regx)) {
                return true;
              }
            });
            if (valIndex.length == values.length) {
              questionDetails.push(this.leadDataTableRef.questionAnsweredList[index]);
              return val;
            }
          }
        });
        break;
      }
      case 5: {
        this.leadDataTableRef.data = this.leadDataTableRef.data.filter((val, index) => {
          if (this.leadDataTableRef.questionAnsweredList[index][questionNumber] && this.leadDataTableRef.questionAnsweredList[index][questionNumber].label && this.leadDataTableRef.questionAnsweredList[index][questionNumber].label != 'Not Answered') {
            const valIndex = values.findIndex(v => {
              const regx = new RegExp("^" + v, "i");
              if (this.leadDataTableRef.questionAnsweredList[index][questionNumber].label.match(regx)) {
                return true;
              }
            });
            if (valIndex != -1) {
              questionDetails.push(this.leadDataTableRef.questionAnsweredList[index]);
              return val;
            }
          }
        });
        break;
      }
      case 6: {
        this.leadDataTableRef.data = this.leadDataTableRef.data.filter((val, index) => {
          if (this.leadDataTableRef.questionAnsweredList[index][questionNumber] && this.leadDataTableRef.questionAnsweredList[index][questionNumber].label && this.leadDataTableRef.questionAnsweredList[index][questionNumber].label != 'Not Answered') {
            const valIndex = values.findIndex(v => {
              const regx = new RegExp(v + '$', "i");
              if (this.leadDataTableRef.questionAnsweredList[index][questionNumber].label.match(regx)) {
                return true;
              }
            });
            if (valIndex != -1) {
              questionDetails.push(this.leadDataTableRef.questionAnsweredList[index]);
              return val;
            }
          }
        });
        break;
      }
      case 7: {
        this.leadDataTableRef.data = this.leadDataTableRef.data.filter((val, index) => {
          if (this.leadDataTableRef.questionAnsweredList[index][questionNumber] && this.leadDataTableRef.questionAnsweredList[index][questionNumber].label &&
            !this.leadDataTableRef.questionAnsweredList[index][questionNumber].label.match(/Not Answered/i)) {
            questionDetails.push(this.leadDataTableRef.questionAnsweredList[index]);
            return val;
          }
        });
        break;
      }
      case 8: {
        this.leadDataTableRef.data = this.leadDataTableRef.data.filter((val, index) => {
          if (!(this.leadDataTableRef.questionAnsweredList[index][questionNumber]) || this.leadDataTableRef.questionAnsweredList[index][questionNumber].label.match(/Not Answered/i) || !(this.leadDataTableRef.questionAnsweredList[index][questionNumber].label)) {
            questionDetails.push(this.leadDataTableRef.questionAnsweredList[index]);
            return val;
          }
        });
        break;
      }
    }
  }

